

var Toast = require('./toast.js').Toast;
var Event = require('mod/event.js');

function Note(opts) {
    this.initOpts(opts);
    this.createNote();
    this.setStyle();
    this.bindEvent();
}

Note.prototype = {
    colors: [
        '#efb04e', // headColor, containerColor
        '#e672a2',
        '#f2eb67',
        '#d15a39',
        '#d0d25c',
        '#5591d2'
    ],
    defaultOpts: {
        id: '',   //Note id
        $ct: $('.container'),  // Note 
        context: 'input here'  //Note 
    },
    initOpts: function (opts) {
        this.opts = $.extend({}, this.defaultOpts, opts || {});
        if (this.opts.id) {
            this.id = this.opts.id;
        }
    },
    createNote: function () {
        var tpl = '<div class="note">'+
                    '<span class="delete">'+
                        '<i class="iconfont icon-delete"></i>'+
                    '</span>'+
                    '<div class="sticky"></div>'+
                    '<div class="note-content" contenteditable=true ></div>'+
                    '<div class="note-date"></div>'+
                    '<span class="username"></span>'
                '</div>';
        this.$note = $(tpl);
        this.$note.find('.note-content').text(this.opts.context);
        this.$note.find('.username').text(this.opts.username);
        this.opts.$ct.append(this.$note);
        Event.fire('waterfall');
    },
    setStyle: function () {
        var index = Math.floor(Math.random() * 6);
        this.$note[0].style.background = 'linear-gradient(35deg,transparent 1.5em,'+this.colors[index]+' 0)'
    },
    setLayout: function () {
        var self = this;
        if (self.clk) {
            clearTimeout(self.clk);
        }
        self.clk = setTimeout(function () {
            Event.fire('waterfall');
        }, 50);
    },
    bindEvent: function () {
        var self = this,
            $note = this.$note,
            $noteHead = $note.find('.sticky'),
            $noteCt = $note.find('.note-content'),
            $delete = $note.find('.icon-delete');

        $delete.on('click', function () {
            self.delete();
        })

        //contenteditable change  save
        $noteCt.on('focus', function () {
            if ($noteCt.html() == 'input here') $noteCt.html('');
            $noteCt.data('before', $noteCt.html());
        }).on('blur paste', function () {
            if ($noteCt.data('before') != $noteCt.html()) {
                $noteCt.data('before', $noteCt.html());
                self.setLayout();
                if (self.id) {
                    self.edit($noteCt.html())
                } else {
                    self.add($noteCt.html())
                }
            }
        });

        //
        $noteHead.on('mousedown', function (e) {
            var evtX = e.pageX - $note.offset().left,   //evtX  dialog dialog 
                evtY = e.pageY - $note.offset().top;
            $note.addClass('draggable').data('evtPos', { x: evtX, y: evtY }); // dialog 
        }).on('mouseup', function () {
            $note.removeClass('draggable').removeData('pos');
        });

        $('body').on('mousemove', function (e) {
            $('.draggable').length && $('.draggable').offset({
                top: e.pageY - $('.draggable').data('evtPos').y,    //  dialog 
                left: e.pageX - $('.draggable').data('evtPos').x
            });
        });
    },
    delete: function () {
        var self = this;
        $.post('/api/notes/delete', { id: this.id })
        .done(function (ret) {
            if (ret.status === 0) {
                Toast('delete success');
                self.$note.remove();
                Event.fire('waterfall')
            } else {
                Toast(ret.errorMsg);
            }
        });
    },
    edit: function (msg) {
        var self = this;
        $.post('/api/notes/edit', {
            id: this.id,
            note: msg
        }).done(function (ret) {
            if (ret.status === 0) {
                Toast('update success');
            } else {
                Toast(ret.errorMsg);
            }
        })
    },
    add: function (msg) {
        console.log('addd...');
        var self = this;
        $.post('/api/notes/add', { note: msg })
        .done(function (ret) {
            if (ret.status === 0) {
                Toast('add success');
            } else {
                self.$note.remove();
                Event.fire('waterfall')
                Toast(ret.errorMsg);
            }
        });
    },
    delete: function () {
        var self = this;
        $.post('/api/notes/delete', { id: this.id })
        .done(function (ret) {
            if (ret.status === 0) {
                Toast('delete success');
                self.$note.remove();
                Event.fire('waterfall')
            } else {
                Toast(ret.errorMsg);
            }
        });
    }
}

module.exports.Note = Note;