require('less/toast.less')
var $ = require('jquery')

function toast(msg,time) {
    this.msg = msg;                     //
    this.dismissTime = time||1000;     //
    this.createToast();                 //toast
    this.showToast();                   //toast
}

toast.prototype = {
    createToast: function () {
        var temp = '<div class="toast">'+this.msg+'</div>';     //
        this.$toast = $(temp);                                  //jQuery
        $('body').append(this.$toast);                          //html
    },
    showToast: function () {
        var _this = this;
        this.$toast.fadeIn(300, function () {
            setTimeout(function () {
                _this.$toast.fadeOut(300,function () {
                    _this.$toast.remove();
                });
            },_this.dismissTime);
        })
    }
};

function Toast(msg,time) {
    return new toast(msg, time); 
}


module.exports.Toast = Toast;       //exportsToast